import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { map, mergeMap, mergeAll, share } from 'rxjs/operators';
import { environment } from './../../environments/environment';
import { catchError, tap } from 'rxjs/operators';
import { UserService } from './user.service';

// Typescript custom enum for search types (optional)
export enum CaseType {
  all = '0',
  active = '1',
  pending = '2',
  closed = '3',
  silent = '4'
}
 
@Injectable({
  providedIn: 'root'
})
export class CaseService {
  url = environment.baseUrl;
  apiKey = ''; // <-- Enter your own key here!
  // userId = 3;

  /**
   * Constructor of the Service with Dependency Injection
   * @param http The standard Angular HttpClient to make requests
   */
  constructor(private http: HttpClient, private userService: UserService) { }
  
  userId() {
    this.userService.getUserId();
  }
  userInfo() {
    return this.userService.getUserId()
  }
  /**
  * Get data from the OmdbApi 
  * map the result to return only the results that we need
  * 
  * @param {string} title Search Term
  * @param {CaseType} type movie, series, episode or empty
  * @returns Observable with the search results
  */
  searchData(title: string, type: CaseType): Observable<any> {
    return this.http.get(`${this.url}?s=${encodeURI(title)}&type=${type}&apikey=${this.apiKey}`).pipe(
      map(results => results['Search'])
    );
  }

  me() {
    return this.http.post(`${this.url}/me`, {})
    .pipe(
      tap(_ => this.userService.log('fetched books')),
      catchError(this.userService.handleError('getBooks', []))
    );
  }

  mapCases(data: any){
    let statusText = 'Active';
    let statusColor = 'secondary';
    switch (data.status) {
      case '2':
        statusText = 'Pending';
        statusColor = 'danger';
        break;
      case '3':
        statusText = 'Closed';
        statusColor = 'success';
        break;
      default:
      statusText = 'Active';
        break;
    }

    let participants = [];
    // return this.getCaseParticipants(data.id);
    // .subscribe((item: any) => {
    //   return item;
    //   // return {
    //   //   ...data,
    //   //   statusText,
    //   //   statusColor,
    //   //   lastAssignee: 'Paul Lieberman',
    //   //   callStatus: 'Accepted',
    //   //   callStatusColor: 'primary',
    //   //   from: 'CuraCall Message',
    //   //   participants: item
    //   // }
    // });

    return {
      ...data,
      statusText,
      statusColor,
      lastAssignee: 'Paul Lieberman',
      callStatus: statusText,
      callStatusColor: statusText === 'Accepted' ? 'primary' : 'warning',
      from: 'CuraCall Message',
      participants
    }
  }
 
  getCases(param: any) {
    return this.http.get(`${this.url}/case`, {params: param})
    .pipe(
      tap(_ => this.userService.log('fetched books')),
      catchError(this.userService.handleError('getBooks', []))
    );

  }

  getCase(caseId: any, userId: any) {
    return this.http.get(`${this.url}/case/${caseId}?user_id=${userId}`);
  }

  getCaseCount(userId: any) {
    return this.http.get(`${this.url}/case-counter/${userId}`);
  }

  getCaseParticipants(caseId) {
    return this.http.get(`${this.url}/case-participants?case_id=${caseId}`);
  }

  casePreview(caseId) {
    return this.http.get(`${this.url}/case-preview/${caseId}/${this.userService.getUserId()}`);
  }
 
  acceptCase(case_id: any, user_id: any) {
    return this.http.post(`${this.url}/case-to-accept`, {case_id, user_id});
  }
 
  closeCase(params: any) {
    return this.http.post(`${this.url}/case-close`, params);
  }
 
  caseReopen(params: any) {
    return this.http.post(`${this.url}/case-reopen`, params);
  }
 
  addNote(params: any) {
    return this.http.post(`${this.url}/case-note/${params.id}`, params);
  }
 
  forwardCase(params: any) {
    return this.http.post(`${this.url}/case-forward`, params);
  }
 
  readCase(params: any) {
    return this.http.post(`${this.url}/case-read/${params.id}`, {params: params});
  }
}
