import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { map, mergeMap, mergeAll, share } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { catchError, tap } from 'rxjs/operators';
import { UserService } from './user.service';
@Injectable({
  providedIn: 'root'
})
export class MessageService {
  url = environment.baseUrl;

  constructor(private http: HttpClient, private userService: UserService) { }
  
  userId() {
    this.userService.getUserId();
  }
  userInfo() {
    return JSON.parse(localStorage.getItem('user'));
  }
  
  getMessages() {
    return this.http.get(`${this.url}/messages`);
  }
  
  getUnreadCount() {
    return this.http.get(`${this.url}/message/room/unread`);
  }
  
  getCheckIfIsRead(param: any) {
    return this.http.get(`${this.url}/message/room/isread`, {params: param});
  }
  
  getRecentContacts(param: any) {
    return this.http.get(`${this.url}/message/recent`, param);
  }
  
  deleteAllMessages(param: any) {
    return this.http.post(`${this.url}/message/delete/all`, param);
  }
  
  createRoom(param: any) {
    return this.http.post(`${this.url}/message/create/room`, param);
  }
  
  updatePusherRoomId(param: any) {
    return this.http.post(`${this.url}/message/update/roompusher`, param);
  }
  
  getConversations(id: any) {
    return this.http.get(`${this.url}/messages/${id}`);
  }
  
  storeConversations(param: any) {
    return this.http.post(`${this.url}/messages`, param);
  }
  
  deleteMessage(id: any) {
    return this.http.delete(`${this.url}/messages/${id}`);
  }
}
